export default {
	data() {
		return {
		
		}
	},
	methods: {
		getUserInfo(res){
			let {userInfo,rawData} = res.detail;
			if(userInfo){
				// 弹出授权成功提示窗口
				uni.showToast({
					title:"授权成功",
					success:() => {
						uni.reLaunch({
							url:"/pages/personal/personal?userInfo="+JSON.stringify(userInfo)
						})
					}
				})
				// 将登录时候的用户信息存储到storage中
				uni.setStorageSync("userInfo",rawData)
			}else{
				// 授权失败提示窗口
				uni.showToast({
					title:"授权失败,请重新授权",
					icon:"none"
				})
			}
		}
	}
}
